

<template>

<div class="banner">
    <div class="wp">
        <div style="position: absolute;min-width: 1080px;">
            <h2>我们追求极致并专注创新</h2>
            <h3>本服务平台为商城，APP提供数据支持，登录后可以测试数据接口。</h3>
            <el-form :model="form" :rules="rules" ref="form" label-position="left" label-width="0px">
              <el-form-item prop="username">
                <el-input type="text" v-model="form.username" auto-complete="off" placeholder="账号"></el-input>
              </el-form-item>
              <el-form-item prop="password">
                <el-input type="password" v-model="form.password" auto-complete="off" placeholder="密码"></el-input>
              </el-form-item>
              <el-form-item style="width:100%;">
                <el-button type="success" style="width:100%;" @click.native.prevent="Submit" :loading="logining">登录</el-button>
              </el-form-item>
            </el-form>
            <h5>当前版本：Maizhong-rest v{{baseInfo.version}} 接口数量：{{baseInfo.interfaceCount }}</h5>
        </div>
    </div>
    <div class="footer">
        <div class="wp">
            <p>
                Copyright© 2014-2017
                <span class="pipe">|</span> XushdTeam 版权所有
                <span class="pipe">|</span>
            </p>
        </div>
    </div>
</div>

</template>

<script>

import * as API from '../api/api'
import NProgress from 'nprogress'
export default {
  data(){
    return {
      baseInfo:{
        version:'',
        interfaceCount:''
      },
      form:{

      },
      rules:{
        username:{required:true,message:'请输入帐号',trigger:'blur'},
        password:{required:true,message:'请输入密码',trigger:'blur'}
      },
      logining: false
    }
  },
  methods:{
    Submit(ev){
      this.$refs.form.validate((valid) => {
        var _this = this;
         if (valid) {
           this.logining = true;
           NProgress.start();
           var loginParam = {username:this.form.username,password:this.form.password};
           API.loginCheck(loginParam).then(res=>{
             this.logining =false;
             NProgress.done();
             let {status,message,data} = res;
             if(status!==200){
               this.$message.error(message);
             }else{
               this.$message({
                  message: '登录成功',
                  type: 'success'
                });
               sessionStorage.setItem('user', JSON.stringify(data));
               this.$router.push({ path: '/' });
             }

           });
         } else {
           console.log('error submit!!');
           return false;
         }
       });
    },
    init(){
      API.getBaseInfo().then(res=>{
        if(res.status==200){
          this.baseInfo = res.data;
        }
      });
    }
  },
  mounted(){
    this.init();
  }
}

</script>
<style media="screen">


.wp {
    width: 1080px;
    min-width: 1080px;
    margin: 0 auto
}
.banner {
    position: relative;
    text-align: center;
    min-width: 1080px
}

.banner h2 {
    color: #fff;
    font-size: 50px;
    font-weight: 700;
    padding-top: 150px
}

.banner h3 {
    color: #fff;
    font-size: 16px;
    font-weight: 100;
    margin-top: 30px;
    line-height: 34px
}

.banner h4 a {
    height: 38px;
    line-height: 38px;
    border: 1px solid #fff;
    font-size: 20px;
    color: #fff;
    transition: all .5s;
    -webkit-transition: all .5s;
    display: block;
}

.banner h4 a:hover {
    border: 1px solid #f9f9f4;
    color: #f9f9f9;
    border-radius: 30px
}

.banner h5 {
    font-weight: 100;
    font-size: 12px;
    color: #fff;
    margin-top: 20px;
    margin-left: -50px;
}

.banner h5 a {
    color: #fff
}

.banner h5 span {
    padding: 0 8px
}

.footer {
    position: fixed;
    bottom: 0px;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    width: 100%;
    min-width: 1080px;
    text-align: center;
    padding: 30px 0;
}

.footer p,
.footer p a {
    font-size: 14px;
    font-weight: 100;
    color: #fff
}

.footer .pipe {
    margin: 0 4px 0 5px;
    color: #fff
}

.footer img {
    margin: -5px 5px 0 0
}


form {
    width: 365px;
    margin: 0 auto;
    padding-top: 30px;
}


</style>



