<template lang="html">
  <el-form ref="form" :model="form" rel="form"  label-width="90px"  style="margin:20px;width:60%;min-width:600px;">
		<el-form-item label="接口URL" prop="url":rules="{required: true, message: '接口URL不能为空', trigger: 'blur'}">
			<el-input v-model="form.url" placeholde="接口URL">
        <template slot="prepend">http://</template>
      </el-input>
		</el-form-item>
		<el-form-item label="请求方式" prop="type" :rules="{required:true,message:'请求方式',trigger:'change'}">
			<el-select v-model="form.type" placeholder="请求方式" >
				<el-option label="POST" value="POST"></el-option>
				<el-option label="GET" value="GET"></el-option>
			</el-select>
		</el-form-item>
		<el-form-item label="参数">
      <el-button type="primary" @click="addParam">新增参数</el-button>
    </el-form-item>
    <el-form-item v-for="(param, index) in form.params" :label="'参数'+ (index+1)" :key="param.key">
     	<el-col :span="5">
        <el-form-item :prop="'params.' + index + '.key'" :rules="{required:true,message:'参数KEY不能为空',trigger:'blur'}">
          <el-input v-model="param.key" placeholder="参数KEY"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="10" :offset="1">
        <el-form-item :prop="'params.' + index + '.value'" :rules="{required:true,message:'参数VALUE不能为空',trigger:'blur'}">
          <el-input v-model="param.value" placeholder="参数VALUE"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="5" :offset="1">
       <el-button @click.prevent="removeParam(param)" type="danger">删除</el-button>
     </el-col>
   </el-form-item>
		<el-form-item>
			<el-button type="success" @click="Submit" :loading="logining" >测试</el-button>
		</el-form-item>
    <div v-show="result!=''">
        <h2>结果</h2>
        <el-card class="box-card">
          {{result}}
        </el-card>
    </div>

	</el-form>

</template>

<script>
import * as API from '../api/api'
export default {
  data() {
    return {
      form: {
        url: '',
        type:'',
        params:[]
      },
      logining:false,
      result:''
    }
  },
  methods: {
    Submit() {
      this.$refs.form.validate((valid) => {
          if (valid) {
            this.logining = true;
            var ajaxParam = {};
            for(var item of this.form.params){
              ajaxParam[item.key] = item.value;
            }
            if(this.form.type=='POST'){
              API.ajaxPost(this.form.url,ajaxParam).then(res=>{
                this.logining = false;
                this.result = res;
              });
            }else{
              API.ajaxGet(this.form.url,ajaxParam).then(res=>{
                this.logining = false;
                this.result = res;
              });
            }

          } else {
            console.log('error submit!!');
            return false;
          }
      });
    },
    addParam(){
      this.form.params.push({});
    },
    removeParam(param){
      var index = this.form.params.indexOf(param);
        if (index !== -1) {
          this.form.params.splice(index, 1);
        }
    }
  }
}
</script>

<style lang="css">

</style>



