import Vue from 'vue';
import VueResource from 'vue-resource';
Vue.use(VueResource);
Vue.http.options.emulateJSON = true;
//基础数据接口
export const getBaseInfo = params => {return Vue.http.get(`/rest/baseInfo`,params).then(res=>res.data);}

//登录验证接口
export const loginCheck = params=>{ return Vue.http.post(`/rest/loginCheck`,params).then(res=>res.data);};

//通用get接口
export const ajaxGet = (url,params)=>{return Vue.http.get(url,params).then(res=>res.data);}

//通用post接口
export const ajaxPost = (url,params)=>{return Vue.http.post(url,params).then(res=>res.data);}

//获取接口列表信息接口
export const getInterfaceList = params=>{return Vue.http.post(`/rest/interfaceList`,params).then(res=>res.data);}



