import LOGIN from './components/login'
import NOTFOUND from './components/404'
import ADMIN from './components/admin'
import INTERFACETEST from './components/interfaceTest'
import INTERFACELIST from './components/interfaceList'
let routes=[
  {
    path:'/login',
    component:LOGIN,
    name:'',
    hidden:true
  },
  {
    path:'/404',
    component:NOTFOUND,
    name:'',
    hidden:true
  },
  {
    path: '/',
    component: ADMIN,
    name: '',
    children: [
      { path: '/interfaceTest', component: INTERFACETEST,iconCls:'el-icon-information',name:'接口测试' },
      { path: '/interfaceList', component: INTERFACELIST,iconCls:'el-icon-star-on',name:'接口列表' }
    ],
    hidden:false
  }
];
export default routes;



