<template lang="html">

<el-row>
   <el-col :span="12">
    <div style="margin: 15px 0;">
      <el-input placeholder="请输入内容" v-model="searchText">
        <el-select v-model="searchType" slot="prepend" placeholder="请选择">
          <el-option label="接口名称" value="interfaceName"></el-option>
          <el-option label="接口URL" value="interfaceUrl"></el-option>
        </el-select>
        <el-button slot="append" icon="search" @click="search"></el-button>
      </el-input>

    </div>
  </el-col>
  <el-table :data="tableData" border style="width: 100%" v-loading="loading"  element-loading-text="拼命加载中">
    <el-table-column label="接口名称" width="180">
       <template scope="scope">
         <span style="margin-left: 10px">{{ scope.row.interfaceName }}</span>
       </template>
     </el-table-column>
     <el-table-column label="接口URL" width="200">
        <template scope="scope">
          <span >{{ scope.row.interfaceUrl }}</span>
        </template>
      </el-table-column>
      <el-table-column label="接口参数" width="200">
         <template scope="scope">
           <span >{{ scope.row.interfaceParam }}</span>
         </template>
       </el-table-column>
       <el-table-column label="请求方式" width="200">
          <template scope="scope">
            <span >{{ scope.row.interfaceType }}</span>
          </template>
        </el-table-column>
       <el-table-column label="接口描述" >
          <template scope="scope">
            <span >{{ scope.row.interfaceDesc }}</span>
          </template>
        </el-table-column>
  </el-table>
  <el-row style="margin:10px" justify="end" type="flex" v-show="total>0" >
    <el-pagination @current-change="handleCurrentChange" :current-page="cur" :page-size="pageSize" layout="total, prev, pager, next" :total="total">
       </el-pagination>

  </el-row>
</el-row>




</template>

<script>
import * as API from '../api/api'
export default {
  data(){
    return {
      searchText:"",
      searchType:"interfaceName",
      loading:true,
      tableData:[],
      total:0,
      pageSize:10,
      currentPage:1
    }
  },
  methods:{
    initDat(){
      let params ={pageIndex:1,pageSize:this.pageSize};
      this.netWork(params);
    },
    handleCurrentChange:function(val){
      let params ={pageIndex:val,pageSize:this.pageSize};
      this.netWork(params);
    },
    search:function(){
      let params ={pageIndex:1,pageSize:this.pageSize,searchFileds:{}}
      if(this.searchText){
        params.searchFileds[this.searchType] = this.searchText
        this.netWork(params)
      }
    },
    netWork:function(params){
      API.getInterfaceList(params).then(res=>{
          this.loading = false;
          let{status,message,data} = res;
          if(status==200){
            this.tableData = data.rows;
            this.total = data.total;
          }
      });
    }
  },
  mounted(){
    this.initDat()
  }
}
</script>

<style>
  .el-select .el-input {
    width: 110px;
  }
</style>



